@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap');

.skills {
  background: #f4f7f6; /* Light grey background */
  color: #333333; /* Dark grey text */
  padding: 100px 50px;
  font-family: 'Montserrat', sans-serif;
  text-align: center;

  /* Animation styles */
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.skills.is-visible {
  opacity: 1;
  transform: translateY(0);
}

.skills h2 {
  font-size: 42px;
  margin-bottom: 40px;
  color: #00405c; /* Deep professional blue */
}

.skills h2 span {
  color: #5d9cec; /* Brighter blue accent */
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  max-width: 1000px;
  margin: auto;
}

.skill-card {
  background-color: #ffffff; /* White background */
  border-radius: 15px;
  padding: 25px;
  box-shadow: 0 0 20px rgba(0, 64, 92, 0.1); /* Shadow with new color */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: center;
}

.skill-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 20px rgba(0, 64, 92, 0.15);
}

.skill-card h3 {
  font-size: 20px;
  margin-bottom: 15px;
  color: #00405c; /* Deep professional blue */
}

.skill-card ul {
  list-style: none;
  padding: 0;
}

.skill-card li {
  font-size: 16px;
  margin-bottom: 8px;
  color: #333333; /* Standard text color */
}

/* Responsive */
@media (max-width: 768px) {
  .skills {
    padding: 70px 20px;
  }

  .skills h2 {
    font-size: 32px;
  }
}