@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap');

.about {
  background-color: #f4f7f6; /* Light grey background */
  color: #333333; /* Dark grey text */
  padding: 120px 50px;
  text-align: center;
  font-family: 'Montserrat', sans-serif;

  /* Animation styles */
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.about.is-visible {
  opacity: 1;
  transform: translateY(0);
}

.about-content {
  max-width: 900px;
  margin: auto;
}

.about h2 {
  font-size: 42px;
  margin-bottom: 40px;
  color: #00405c; /* Deep professional blue */
}

.about h2 span {
  color: #5d9cec; /* Brighter blue accent */
}

.about-box {
  background-color: #ffffff; /* White background */
  border-left: 4px solid #5d9cec; /* Brighter blue accent */
  border-radius: 8px;
  padding: 30px 25px;
  margin-bottom: 30px;
  box-shadow: 0 0 20px rgba(0, 64, 92, 0.1); /* Shadow with new color */
  text-align: left;
}

.about-box p {
  font-size: 16px;
  line-height: 1.8;
  margin-bottom: 16px;
  color: #333333; /* Standard text color */
}

.about-box strong {
  color: #00405c; /* Deep professional blue */
}

.btn {
  margin-top: 20px;
  background-color: #5d9cec; /* Brighter blue accent */
  color: #ffffff; /* White text */
  border: none;
  padding: 12px 28px;
  font-size: 15px;
  font-weight: 600;
  border-radius: 30px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.btn:hover {
  background-color: #00405c; /* Deep professional blue */
  color: #ffffff;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive */
@media (max-width: 768px) {
  .about {
    padding: 80px 20px;
  }

  .about h2 {
    font-size: 32px;
  }

  .about-box {
    text-align: center;
  }
}