@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap');

.contact {
  background: #f4f7f6; /* Light grey background */
  color: #333333; /* Dark grey text */
  padding: 100px 50px;
  font-family: 'Montserrat', sans-serif;
  text-align: center;
}

.contact h2 {
  font-weight: 700;
  font-size: 42px;
  margin-bottom: 40px;
  color: #00405c; /* Deep professional blue */
}

.contact h2 span {
  color: #5d9cec; /* Brighter blue accent */
}

.contact-form {
  max-width: 800px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr;
  gap: 25px;
}

.contact-form input,
.contact-form textarea {
  background-color: #ffffff; /* White background */
  border: 1px solid #ccc;
  padding: 15px 20px;
  border-radius: 8px;
  color: #333333; /* Dark grey text */
  font-size: 16px;
  font-weight: 500;
  resize: none;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
  width: 100%;
  box-sizing: border-box;
}

.contact-form input::placeholder,
.contact-form textarea::placeholder {
  color: #999;
}

.contact-form input:focus,
.contact-form textarea:focus {
  border-color: #5d9cec; /* Brighter blue accent on focus */
  box-shadow: 0 0 8px rgba(93, 156, 236, 0.3);
  outline: none;
}

.contact-form textarea {
  min-height: 160px;
}

.btn {
  background: #5d9cec; /* Brighter blue accent */
  color: #ffffff;
  padding: 15px 0;
  font-size: 18px;
  font-weight: 700;
  border: none;
  border-radius: 40px;
  cursor: pointer;
  letter-spacing: 1.5px;
  transition: background-color 0.4s ease, transform 0.2s ease;
}

.btn:hover {
  background: #00405c; /* Deep professional blue on hover */
  transform: scale(1.05);
}

@media (max-width: 600px) {
  .contact {
    padding: 60px 20px;
  }
}
