.home {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background-color: #f4f7f6; /* Light grey background */
  color: #333333; /* Dark grey text */
  padding: 100px 50px;
  font-family: 'Montserrat', sans-serif;
  flex-wrap: wrap;
  gap: 50px;
}

/* Left side content */
.home-left {
  max-width: 600px;
  flex: 1;
}

.home-left h2,
.home-left h3,
.home-left h1 {
  margin: 0;
  color: #00405c; /* Deep professional blue for headings */
}

.home-left h1 {
  font-size: 52px;
  margin: 10px 0;
}

.home-left h3 {
  font-size: 24px;
  margin-bottom: 20px;
}

.home-left h3 span {
  color: #5d9cec; /* Brighter blue accent */
  font-weight: 600;
}

.bio-intro {
  font-size: 16px;
  margin-bottom: 20px;
  line-height: 1.6;
  color: #333333; /* Standard text color */
}

.bio-details p {
  margin: 5px 0;
  font-size: 15px;
  color: #333333;
}

.bio-details strong {
  color: #00405c; /* Deep blue for emphasis */
  display: inline-block;
  width: 80px;
}

.social-icons {
  margin-top: 20px;
}

.social-icons i {
  color: #00405c; /* Deep blue for icons */
  margin-right: 15px;
  font-size: 22px;
  cursor: pointer;
  transition: transform 0.3s ease, color 0.3s ease;
}

.social-icons i:hover {
  transform: scale(1.2);
  color: #5d9cec; /* Brighter blue on hover */
}

/* Right side image */
.home-right {
  flex: 1;
  display: flex;
  justify-content: center;
}

.profile-img {
  width: 300px;
  border-radius: 360px;
  box-shadow: 0 0 30px rgba(0, 64, 92, 0.2); /* Shadow with the deep blue color */
  transition: transform 0.4s ease-in-out;
  border: 5px solid #e0e0e0; /* Light grey border */
}

.profile-img:hover {
  transform: scale(1.05);
}

/* Education section */
.education-section {
  width: 100%;
  margin-top: 80px;
  background-color: #ffffff; /* White background for contrast */
  padding: 40px;
  border-radius: 8px;
  border: 1px solid #e0e0e0; /* Light grey border */
  box-shadow: 0 0 20px rgba(0, 64, 92, 0.1); /* Shadow with the new color scheme */
}

.education-section h2 {
  font-size: 32px;
  margin-bottom: 30px;
  color: #00405c; /* Deep professional blue */
  text-align: center;
}

.education-item {
  margin-bottom: 25px;
  border-left: 4px solid #5d9cec; /* Brighter blue accent */
  padding-left: 15px;
}

.education-item h3 {
  font-size: 20px;
  margin-bottom: 5px;
  color: #00405c; /* Deep professional blue */
}

.education-item p {
  font-size: 15px;
  color: #333333; /* Standard text color */
  line-height: 1.5;
}

/* Responsive styles */
@media (max-width: 768px) {
  .home {
    flex-direction: column;
    text-align: center;
    padding: 80px 20px;
  }

  .home-right {
    margin-top: 30px;
  }

  .profile-img {
    width: 220px;
  }

  .education-section {
    padding: 30px 20px;
    margin-top: 60px;
  }
}
