@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap');

.projects {
  background: #f4f7f6; /* Light grey background */
  color: #333333; /* Dark grey text */
  padding: 100px 50px;
  font-family: 'Montserrat', sans-serif;
  text-align: center;

  /* Animation styles */
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.projects.is-visible {
  opacity: 1;
  transform: translateY(0);
}

.projects h2 {
  font-size: 42px;
  margin-bottom: 50px;
  color: #00405c; /* Deep professional blue */
}

.projects h2 span {
  color: #5d9cec; /* Brighter blue accent */
}

.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  max-width: 1100px;
  margin: auto;
}

.project-card {
  background-color: #ffffff; /* White background */
  border: 1px solid transparent; /* Add transparent border to prevent layout shift */
  border-radius: 15px;
  padding: 25px;
  box-shadow: 0 0 20px rgba(0, 64, 92, 0.1); /* Shadow with new color */
  transition: transform 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease; /* Add border-color to transition */
  text-align: left;
}

.project-card:hover {
  transform: translateY(-10px);
  border-color: #5d9cec; /* Accent blue border on hover */
  box-shadow: 0 12px 28px rgba(93, 156, 236, 0.25); /* More vibrant accent shadow */
}

.project-card h3 {
  color: #00405c; /* Deep professional blue */
  font-size: 20px;
  margin-bottom: 12px;
}

.project-card p {
  color: #333333; /* Standard text color */
  font-size: 15px;
  line-height: 1.6;
}

.project-links a {
  color: #5d9cec; /* Brighter blue accent */
  text-decoration: none;
  margin-right: 1rem;
  font-weight: 600;
}

.project-links a:hover {
  text-decoration: underline;
}

/* Responsive */
@media (max-width: 768px) {
  .projects {
    padding: 60px 20px;
  }

  .projects h2 {
    font-size: 32px;
  }
}
